

















import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import { sysTime } from "../api";
import { sendMsg } from "../msg";
import bus from "../bus";

@Component
export default class Status extends Vue {
  platform = "taobao";
  status_text = "";
  checkStatus() {
    bus.$emit("check-status");
  }
  sysTime() {
    sysTime(this.platform);
  }
}
